import React from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';

import './Shareplex.css'

const getValues = (content)=>{
  let regex = /BDU:(.|\n)*?Number_Conflicts_To_Process: (\d*)/ig;
  let match = regex.exec(content);
  const bduToProcess = Number(match[2]);
  
  regex = /BDU:(.|\n)*?Number Conflicts To Check: (\d*)/ig;
  match = regex.exec(content);
  const bduToCheck = Number(match[2]);

  regex = /BDU:(.|\n)*?Last Processed Transaction File: (.*?) /ig;
  match = regex.exec(content);
  const datesbdu = match[2];

  regex = /JAX:(.|\n)*?Number_Conflicts_To_Process: (\d*)/ig;
  match = regex.exec(content);
  const jaxToProcess = Number(match[2]);

  regex = /JAX:(.|\n)*?Number Conflicts To Check: (\d*)/ig;
  match = regex.exec(content);
  const jaxToCheck = Number(match[2]);

  regex = /JAX:(.|\n)*?Last Processed Transaction File: (.*?) /ig;
  match = regex.exec(content);
  const datesjax = match[2];

  return {bduToProcess,bduToCheck, datesbdu, jaxToProcess, jaxToCheck, datesjax}
}

class Shareplex extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      value: '',
      conflicts:{
        jaxToProcess:'',
        jaxToCheck:'',
        bduToProcess:'',
        bduToCheck:'',
        datesjax:'',
        datesbdu: ''
      },
      text: ''
    }
  }

  handleChange (e) {
    this.setState({value: e.target.value});
    const conflicts = getValues(e.target.value);
    this.setState({conflicts});
    this.setState({text: `${conflicts.datesjax === conflicts.datesbdu ? conflicts.datesjax : 'ERROR'}\t${conflicts.jaxToProcess}\t${conflicts.jaxToCheck}\t${conflicts.bduToProcess}\t${conflicts.bduToCheck}`});
  }

  render () {
    return (
      <div>
      <form>
        <textarea rows="20" placeholder="put email content here!!!" value={this.state.value} onChange={(e) => {this.handleChange(e)}}/>
      </form>
      <pre id="result">
        <p>
        {this.state.text}        
        </p>
        <br />
        <CopyToClipboard text={this.state.text}
          onCopy={() => alert("Copied to clipboard!")}>
          <button>Copy to clipboard</button>
        </CopyToClipboard>
        <br />
        <a href="https://docs.google.com/spreadsheets/d/14VPI_bwqaUxoeDoAKpUpWqnKcGfz2x-xX_dFE5z-Y5o/edit#gid=1116415703" rel="noreferrer" target="_blank">File</a>
      </pre>
      </div>
    )
  }
}

export default Shareplex;