import React, { Component } from 'react';
import './LogParser.css';

class LogParser extends Component {
  constructor(props) {
    super(props);
    this.state = { inputText: '', outputText: '' };
  }

  handleChange = (event) => {
    const newText = event.target.value.replace(/\\n/g, '\n').replace(/\\+/g, '');
    this.setState({ inputText: event.target.value, outputText: newText });
  };

  render() {
    return (
      <div className="LogParser">
        <div className="textarea-container">
          <label htmlFor="input-textarea">Input Text:</label>
          <textarea
            id="input-textarea"
            value={this.state.inputText}
            onChange={this.handleChange}
          ></textarea>
        </div>
        <div className="textarea-container">
          <label htmlFor="output-textarea">Output Text:</label>
          <textarea
            id="output-textarea"
            value={this.state.outputText}
            readOnly
          ></textarea>
        </div>
      </div>
    );
  }
}

export default LogParser;
