import React, { Component } from 'react';
import { Button } from 'react-bootstrap';

import axios from 'axios';
import moment from 'moment';

//const queueUrl = "https://devsupport.cebroker.com/queuescount";
const MASK = "hh:mm:ss A DD/MM/YY";

let queues = {};
let url = "https://devsupport.cebroker.com/queuescount";

const styles = {
  table: {
    'overflowY': 'scroll'
  },
  tableHeader: {
    tr: { 'minWidth': '280px', 'fontWeight': 'bold' },
    td: { 'minWidth': '280px' },
    dates: {
      td: { 'minWidth': '110px', 'padding': '0 5px', 'width': '110px' }
    }
  }
}

class QueueList extends Component {

  constructor(props) {
    super(props);
    this.state = {
      dates: [],
      queues: {},
      loading: false,
      intervalSec: 30,
      loadingInterval: false,
      interval: null,
      queueUrl: "https://devsupport.cebroker.com/queuescount"
    };
    this.loadData = this.loadData.bind(this);
    this.handleIntervalChange = this.handleIntervalChange.bind(this);
    this.handleURLChange = this.handleURLChange.bind(this);
    this.handleURLKey = this.handleURLKey.bind(this);
    this.loadDataInterval = this.loadDataInterval.bind(this);
    this.deleteHistory = this.deleteHistory.bind(this);
    this.loadDates = this.loadDates.bind(this);
  }

  getQueues(data) {
    let processedData = data.matchAll(/<tr style='color: (.+?);.+?>.+?<td>(.+?)<\/td>.+?<td>(.+?)<\/td>.+?<\/tr>/g);
    //let date = data.match(/<strong>(.+?)<\/strong>/i);
    //let pDate = new moment(date[1], "MM/DD/YYYY, HH:mm:SS A").toDate().getTime();
    let date = new Date();
    processedData = Array.from(processedData);
    processedData = [...new Map(processedData.map((m) => [m[2], m])).values()];
    processedData.forEach(match => {

      queues = this.state.queues;
      let history = queues[match[2]]?.history || [];

      let current = {
        value: match[3],
        color: match[1],
        date
      }

      let prev = history.length > 0 ? history[0] : null;

      if (prev) {
        current.diff = current.value - prev.value;
        if (prev.value > current.value) {
          current.valueShow = `${current.value} (-)`;
        } else if (prev.value < current.value) {
          current.valueShow = `${current.value} (+)`;
        } else {
          current.valueShow = `${current.value} (=)`;
        }
      } else {
        current.valueShow = current.value;
        current.diff = 'N/D';
      }

      history.unshift(
        current
      );

      this.loadDates(history);
      queues[match[2]] = {
        name: match[2],
        history
      };
      this.setState({ queues });
      localStorage.setItem(this.state.queueUrl, JSON.stringify(queues));
    });
  }

  loadDates(history) {
    let dates = history.map(q => {
      return q.date;
    });
    this.setState({ dates });
  }

  componentDidMount() {
    this.loadData();
  }

  handleIntervalChange = (event) => {
    this.setState({ intervalSec: event.target.value });
  }

  handleURLChange = (event) => {
    this.url = event.target.value;
    console.log(url);
  }

  handleURLKey = (event) => {
    if (event.key === 'Enter') {
      this.deleteHistory(true);
      this.setState({ queueUrl: this.url }, () => {
        console.log(this.state.queueUrl);
        this.loadData();
      });
    }
  }

  loadDataInterval = (event) => {
    let interval = this.state.interval;
    if (this.state.loadingSeq) {
      clearInterval(interval);
    } else {
      interval = setInterval(this.loadData, this.state.intervalSec * 1000);
      this.setState({ interval });
    }
    this.setState({ loadingSeq: !this.state.loadingSeq });
  }

  loadData = () => {
    this.setState({ loading: true });
    let storage = localStorage.getItem(this.state.queueUrl);
    if (storage) {
      this.setState({
        queues: JSON.parse(storage)
      }, () => {
        Object.keys(this.state.queues).forEach(queue => {
          this.loadDates(this.state.queues[queue].history);
        })
      })

    }
    axios.get(this.state.queueUrl)
      .then(res => {
        this.getQueues(res.data.item[0].text, this.state.queues);
        this.setState({ loading: false });
      }).catch(err => {
        console.error(err);
        this.setState({ loading: false });
      })
  }

  deleteHistory = (disableAlert) => {
    localStorage.removeItem(this.state.queueUrl);
    this.setState({
      queues: {}
    })

    if (!disableAlert) {
      alert("History deleted!");
    }
  }

  render() {
    return (
      <div className="">
        {Object.keys(this.state.queues).length > 0 ?
          <table border='1' style={styles.table} className="table table-striped" >
            <thead className="thead-dark">
              <tr style={styles.tableHeader.tr}>
                <th style={styles.tableHeader.td} scope="col">Queue</th>
                {
                  this.state.dates.map(dt => {
                    return <th key={dt} style={styles.tableHeader.dates.td} scope="col">{moment(dt).format(MASK)}</th>
                  })
                }
              </tr>
            </thead>
            <tbody>
              {
                Object.keys(this.state.queues).map(queue => {
                  return (
                    <tr key={queue}>
                      <td key={queue} >{queue}</td>
                      {
                        this.state.queues[queue].history.map(history => {
                          return <td style={{ color: history.color }} key={new Date().getTime() + "-" + history.date} title={history.diff}>{history.valueShow}</td>
                        })
                      }
                    </tr>
                  );
                })
              }
            </tbody>
          </table>
          :
          <p color="danger">No data to display!!!</p>
        }
        <div className="container text-center">
          <div className="row">
            <div className="col-3">
              <Button onClick={this.loadData} disabled={this.state.loading} variant={this.state.loading ? "warning" : "primary"} className="form-control col" >{this.state.loading ? "Loading Queues..." : "Load Queues"}</Button>
            </div>
            <div className="col-3">
              <Button onClick={this.deleteHistory} variant="danger" className="form-control col" >Delete History</Button>
            </div>
            <div className="col-1">
              <label htmlFor="txt-interval" className="">Interval:</label >
            </div>
            <div className="col-2">
              <input id="txt-interval" type="number" value={this.state.intervalSec} onChange={this.handleIntervalChange} className="form-control" />
            </div>
            <div className="col-3">
              <Button onClick={this.loadDataInterval} variant={this.state.loadingSeq ? "warning" : "primary"} className="form-control" >{this.state.loadingSeq ? "Stop loading Interval" : "Load Interval"}</Button>
            </div>
          </div>
          <div className="row">
            <div className="col-1">
              <label htmlFor="txt-url">URL:</label>
            </div>
            <div className="col-11">
              <input id="txt-url" type="text" defaultValue={url} onChange={this.handleURLChange} onKeyDown={this.handleURLKey} className="form-control" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default QueueList;