import React from 'react';
import Shareplex from './components/Shareplex';
import QueueList from './components/QueueList';
import './App.css'
import {
  HashRouter,
  Routes,
  Route
} from "react-router-dom";
import LogParser from './components/logParser';

class App extends React.Component {

  render() {
    return (
      <HashRouter basename={process.env.PUBLIC_URL}>
        <div className="App">
          <Routes>
            <Route path="/"  element={<QueueList />} />
            <Route path="/shareplex"  element={<Shareplex />} />
            <Route path="/logs"  element={<LogParser />} />
          </Routes>
        </div>
      </HashRouter>
    )
  }
}

export default App;